  .limiter  .login100-form {
    width: 100%;
  }
  
  .limiter  .login100-form-title {
    font-size: 30px;
    color: #555555;
    line-height: 1.2;
    text-transform: uppercase;
    text-align: center;
  
    width: 100%;
    display: block;
  }
  
  
  
  /*------------------------------------------------------------------
