  .limiter  .wrap-input100 {
    width: 100%;
    position: relative;
    background-color: #fff;
    border: 1px solid #e6e6e6;
    border-radius: 2px;
  }
  
  
  /*---------------------------------------------*/
  .limiter .input100:nth-child(2) {
    width: 93%;

  }

  .MuiTypography-body1 {
    font-size: .9rem !important;
     font-family: inherit !important; 
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
}

  .limiter .input100 {
    color: #555555;
    line-height: 1.2;
    font-size: 16px;
    display: block;
    width: 100%;
    background: transparent;
    height: 41px;
    padding: 0 25px 0 25px;
  }
  /*------------------------------------------------------------------
  [ Focus Input ]*/
  
  .limiter .focus-input100 {
    position: absolute;
    display: block;
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    top: -1px;
    left: -1px;
    pointer-events: none;
    border: 1px solid #b3b3b3;
    border-radius: 3px;
  
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s;
    transform: scaleX(1.1) scaleY(1.3);
  }
  
  .limiter .input100:focus + .focus-input100 {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
  }
  
  .limiter .eff-focus-selection {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
  }
  
  /*---------------------------------------------*/
  .limiter .btn-show-pass {
    font-size: 15px;
    color: #999999;
    display: flex;
    align-items: center;
    position: absolute;
    height: 100%;
    top: 0;
    right: 12px;
    padding: 0 5px;
    cursor: pointer;
    transition: background 0.4s;
  }
  
  .limiter .btn-show-pass:hover {
    color: #000000;
  }
  
  .limiter .btn-show-pass.active {
    color: #000000;
  }
  
