    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  
  .limiter .login100-form-btn {
    font-size: 16px;
    color: #fff;
    line-height: 1.2;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    min-width: 150px;
    height: 48px;
    background-color: #333333;
    border-radius: 2px;
    transition: all 0.4s;
    width: 100%;
  }
  
  .limiter .login100-form-btn:hover {
    background-color: #000;
  }
  
  
  /*------------------------------------------------------------------
  [ Alert validate ]*/
  
  .limiter .validate-input {
    position: relative;
  }
  
  .limiter .alert-validate .btn-show-pass {
    visibility: hidden;
  }
  
  .limiter .alert-validate::before {
    content: attr(data-validate);
    position: absolute;
    max-width: 70%;
    background-color: #fff;
    border: 1px solid #c80000;
    border-radius: 3px;
    padding: 4px 25px 5px 10px;
    top: 50%;
    transform: translateY(-50%);
    right: 12px;
    pointer-events: none;
  
    color: #c80000;
    font-size: 14px;
    line-height: 1.4;
    text-align: left;
  
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.4s;
  }
  
  .limiter .alert-validate::after {
    content: "\f12a";
    display: block;
    position: absolute;
    color: #c80000;
    font-size: 18px;
    top: 50%;
    transform: translateY(-50%);
    right: 18px;
  }
  
  .limiter .alert-validate:hover:before {
    visibility: visible;
    opacity: 1;
  }
  
  @media (max-width: 992px) {
    .limiter .alert-validate::before {
      visibility: visible;
      opacity: 1;
    }
  }
  
  
